import React, { useCallback, useState } from 'react'
import styled from 'styled-components'
import { Button, Text } from '@snowge/uikit'
import { AlertTriangle } from 'react-feather'
import Modal from '../Modal'
import { AutoRow, RowBetween } from '../Row'
import { AutoColumn } from '../Column'

const WarningContainer = styled.div`
  max-width: 420px;
  width: 100%;
  padding: 1rem;
  background: rgba(242, 150, 2, 0.05);
  border: 1px solid #f3841e;
  border-radius: 20px;
  overflow: auto;
`

const fullTokenName = new Map();
fullTokenName.set('CATNIP', 'Catnip.Money');


const StyledWarningIcon = styled(AlertTriangle)`
  stroke: ${({ theme }) => theme.colors.binance};
`

export default function DelistingWarningModal({ isOpen, token, endDate, onConfirm }: { isOpen: boolean; token: string | null; endDate: Date; onConfirm: () => void }) {
  const [understandChecked, setUnderstandChecked] = useState(false)
  const toggleUnderstand = useCallback(() => setUnderstandChecked((uc) => !uc), [])

  const getRemainingDays = () => {
    const now = new Date();
    const diff = endDate.getTime() - now.getTime();
    if (diff < 0) {
      return 0;
    }
    return Math.ceil(diff / (1000 * 3600 * 24));
  }

  const handleDismiss = useCallback(() => null, [])
  return (
    <Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
      <WarningContainer className="token-warning-container">
        <AutoColumn gap="lg">
          <AutoRow gap="6px">
            <StyledWarningIcon />
            <Text>Notice for trading {token}</Text>
          </AutoRow>
          <>
            <Text>The developers of {fullTokenName.get(token)} have decided to hand over control to their community, and are <strong>no longer maintaining their project.</strong></Text>
            <Text>Because of the lack of transparency before this decision, SnowgeSwap can not in good faith keep this token listed.</Text>
            <Text>Following a short grace period, <strong>{fullTokenName.get(token)} ({token}) will be removed from our approved token list.</strong></Text>
            <Text><strong>{getRemainingDays()}</strong> days remaining.</Text>
          </>
          <RowBetween>
            <div>
              <label htmlFor="understand-checkbox" style={{ cursor: 'pointer', userSelect: 'none' }}>
                <input
                  id="understand-checkbox"
                  type="checkbox"
                  className="understand-checkbox"
                  checked={understandChecked}
                  onChange={toggleUnderstand}
                />{' '}
                <Text as="span">I understand</Text>
              </label>
            </div>
            <Button
              id="confirm-checkbox"
              disabled={!understandChecked}
              variant="danger"
              style={{ width: '140px' }}
              onClick={() => {
                setUnderstandChecked(false)
                onConfirm()
              }}
            >
              Continue
            </Button>
          </RowBetween>
        </AutoColumn>
      </WarningContainer>
    </Modal>
  )
}
