const swingIn = keyframes`
  0% {
    height: 0;
    -webkit-transform: rotateX(-100deg);
            transform: rotateX(-100deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 0;
  }
  100% {
    height: auto;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 1;
  }
`;

const AlertWrapper = ({className, title, children, variant, onClick}) => (
    <div className={className}>
      <Alert title={title} variant={variant} onClick={onClick}>{children}</Alert>
    </div>
);

const StyledAlertWrapper = styled(AlertWrapper)`
    > div {
        animation: ${swingIn} 1s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
        width: 100%;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        max-width: 1000px;
        bottom: 0px;
        z-index: 100;
    }
`;

const BigAlertWrapper = styled.div`
  color: black;
`;

