import * as React from "react"
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import styled, { ThemeContext } from 'styled-components';
import discordIcon from "./discord.svg";
import darkDiscordIcon from "./discord-dark.svg";
import twitterIcon from "./twitter.svg";
import darkTwitterIcon from "./twitter-dark.svg";
import telegramIcon from "./telegram.svg";
import darkTelegramIcon from "./telegram-dark.svg";
import redditIcon from "./reddit.svg";
import darkRedditIcon from "./reddit-dark.svg";
import mediumIcon from "./medium.svg";
import darkMediumIcon from "./medium-dark.svg";
import youtubeIcon from "./youtube.svg";
import darkYoutubeIcon from "./youtube-dark.svg";
import githubIcon from "./github.svg";
import darkGithubIcon from "./github-dark.svg";
import twitchIcon from "./twitch.svg";
import darkTwitchIcon from "./twitch-dark.svg";

const Socials = () => {

    const theme = React.useContext(ThemeContext);


    const SocialWrapper = styled.div`
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    `;

    return (
        <SocialWrapper>
            <Tooltip title="Twitter" enterDelay={600}>
                <IconButton aria-label="Twitter" href="https://twitter.com/snowgecoin" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={twitterIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkTwitterIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Telegram" enterDelay={600}>
                <IconButton aria-label="Telegram" href="http://t.me/snowgecoin" target="_blank" style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={telegramIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkTelegramIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Discord" enterDelay={600}>
                <IconButton aria-label="Discord" href="https://discord.gg/qy3ymqazeJ" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={discordIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkDiscordIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Reddit" enterDelay={600}>
                <IconButton aria-label="Reddit" href="https://www.reddit.com/r/SnowgeCoin/" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={redditIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkRedditIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Medium" enterDelay={600}>
                <IconButton aria-label="Medium" href="https://snowgecoin.medium.com" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={mediumIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkMediumIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Youtube" enterDelay={600}>
                <IconButton aria-label="Youtube" href="https://www.youtube.com/channel/UCYXES0sbHwh6xvwA6sRtONQ" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={youtubeIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkYoutubeIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Github" enterDelay={600}>
                <IconButton aria-label="Github" href="https://github.com/snowgecoin" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={githubIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkGithubIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
            <Tooltip title="Twitch" enterDelay={600}>
                <IconButton aria-label="Twitch" href="https://twitch.tv/snowgegaming" target="_blank"
                            style={{color: '#FFFFFF'}}>
                    {theme.isDark
                      ? <img src={twitchIcon} alt='twitter logo' height={18} width={18}/>
                      : <img src={darkTwitchIcon} alt='twitter logo' height={18} width={18}/>}
                </IconButton>
            </Tooltip>
        </SocialWrapper>
    )
};

export default Socials
